import React from "react";
import './css/introCard.css';
import {Card, Col, Row} from "react-bootstrap";
import {GitHub, Mail, MapPin, Phone} from "react-feather";

const IntroCard = () => {
    const phone = '(830) 213-0757';
    const email = 'mando.mg.91@gmail.com';
    const githubRepoURL = 'https://github.com/MandoMG';

    return (
        <Card>
            <Card.Body>
                <Row>
                    <Col sm={7}>
                        <Card.Title>Armando Meza</Card.Title>
                        <Card.Subtitle>Jr. Mobile and Web Developer</Card.Subtitle>
                        <Card.Text
                            className="descriptionSection">{'I\'m a mobile/web developer looking for opportunities to hone and learn skills while also growing as a professional'}</Card.Text>
                    </Col>
                    <Col sm={5}>
                        <Card.Text><MapPin className="iconStyle"/>
                            <span>{'San Marcos, Tx. 78666'}</span></Card.Text>
                        <Card.Text><Phone className="iconStyle"/> <span>{`${phone}`}</span></Card.Text>
                        <Card.Text><Mail className="iconStyle"/> <a href="mailto:mando.mg.91@gmail.com">{`${email}`}</a></Card.Text>
                        <Card.Text>
                            <GitHub className="iconStyle"/>
                            <a href={githubRepoURL} target="_blank" rel="noreferrer">{`${githubRepoURL}`}</a>
                        </Card.Text>
                    </Col>
                </Row>
            </Card.Body>
        </Card>
    );
}

export default IntroCard;