import {Card, Col, Container, ListGroup, ProgressBar, Row} from "react-bootstrap";
import React, {useState} from "react";
import {CardSection, CardSectionDetail} from "../interfaces/resumeSectionItem";

interface TechSkillsSectionComponentProps {
    cardSections: CardSection[];
}

const TechSkillsSectionComponent = ({cardSections}: TechSkillsSectionComponentProps) => {
    const [itemDetailGroup, setItemDetailGroup] = useState<CardSectionDetail[]>();
    const [selectedItemIndex, setSelectedItemIndex] = useState<number>(-1);

    const isActive = (currentIndex: number) => {
        if(selectedItemIndex === -1) {
            return false;
        }

        return currentIndex === selectedItemIndex;
    }

    const handleListItemClick = (command: string) => {
        const selectedCard = cardSections.filter((card: CardSection, index: number) => {
            if(card.sectionCommand !== command) {
                return false;
            }

            setSelectedItemIndex(index);
            return true
        });
        setItemDetailGroup(selectedCard[0].sectionItemDetail);
    }

    return (
        <div>
            <Card>
                <Row>
                    <Col sm={4}>
                        <ListGroup variant="flush">
                            {cardSections.length && cardSections.map((card: CardSection, index: number) => (
                                <ListGroup.Item
                                    onClick={() => handleListItemClick(card.sectionCommand)}
                                    active={isActive(index)}>
                                    {card.sectionTitle}
                                </ListGroup.Item>
                            ))}
                        </ListGroup>
                    </Col>
                    <Col sm={8}>
                        <Container style={{ marginTop: 15, marginBottom: 15 }}>
                            {!!itemDetailGroup && !!itemDetailGroup.length && itemDetailGroup.map((detailItem: CardSectionDetail) => (
                                <>
                                    <Row>
                                        <Col sm={4}>
                                            <p>{detailItem.itemDetail}</p>
                                        </Col>
                                        <Col sm={8}>
                                            <ProgressBar now={detailItem.itemProgress}/>
                                        </Col>
                                    </Row>
                                </>
                            ))}
                        </Container>
                    </Col>
                </Row>
            </Card>
        </div>
    )
}

export default TechSkillsSectionComponent;
