import React from "react";
import {TimelineListGroup, TimelineListItem} from "../interfaces/resumeSectionItem";
import {Col, Row} from "react-bootstrap";
import './css/timeline.css';

interface TimelineComponentProps {
    timelineItemGroup: TimelineListGroup[];
}

const TimelineComponent = ({timelineItemGroup}: TimelineComponentProps) => {
    return (!!timelineItemGroup && timelineItemGroup.length) ? (
        <>
            <Row>
                <Col>
                    <div>
                        <ul className="timeline">
                            {timelineItemGroup.map((item: TimelineListGroup) => (
                                <>
                                    {item.timelineList.map((listItem: TimelineListItem) => (
                                        <li className="event" data-date={listItem.itemDates}>
                                            <h3>{listItem.itemTitle}</h3>
                                            <h4>{item.timelineLocation}</h4>
                                            {!!listItem.itemDescription && (
                                                <ul>
                                                    <li>
                                                        <p>{listItem.itemDescription}</p>
                                                    </li>
                                                </ul>
                                            )}
                                        </li>
                                    ))}
                                </>
                            ))}
                        </ul>
                    </div>
                </Col>
            </Row>
        </>
    ) : (
        <></>
    );
}

export default TimelineComponent;