import React from "react";
import ResumeSectionItem from "../interfaces/resumeSectionItem";
import TechSkillsSectionComponent from "./techSkillsSectionComponent";
import TimelineComponent from "./timelineComponent";

interface ResumeSectionComponentProps {
    sectionItem: ResumeSectionItem
}

const ResumeSectionComponent = ({sectionItem}: ResumeSectionComponentProps) => {
    const {cardSections, description, title, timelineItems} = sectionItem;

    return (
        <div style={{ marginTop: 15 }}>
            <h3>{title}</h3>
            {!!description && (<p>{description}</p>)}
            {!!cardSections && (<TechSkillsSectionComponent cardSections={cardSections} />)}
            {!!timelineItems && (<TimelineComponent timelineItemGroup={timelineItems}/>)}
        </div>
    );
}

export default ResumeSectionComponent;